.categories {
  &__wrapper {
    display: grid;
    @include sectionRowGap;
  }
}

.category {
  &__item {
    @include pageGrid;
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    position: relative;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      padding-bottom: 100%;
      height: 0;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__button {
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;

    @include hover(transform, scale(1.05));
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;

    @include onDesktop {
      font-size: 24px;
    }
  }
}
